

import Vue from 'vue'
import FastClick from 'fastclick'
import VueRouter from 'vue-router'
import App from './App'
import Index from './components/Index'
import LiveRoom from './components/LiveRoom'
import Order from './components/Order'
import Address from './components/AddressList'
import EditAddress from './components/EditAddress'
import OrderSuccess from './components/OrderSuccess'
import MyOrderList from './components/MyOrder'
import axios from 'axios'
import AlertPlugin from 'vux/src/plugins/alert/index.js'
import LoadingPlugin from 'vux/src/plugins/loading/index.js'
import ToastPlugin from 'vux/src/plugins/toast/index.js'


import WechatPlugin from 'vux/src/plugins/wechat/index.js'

import VConsole from 'vconsole'






Vue.use(VueRouter);
Vue.use(AlertPlugin);
Vue.use(LoadingPlugin);
Vue.use(ToastPlugin, {position: 'middle'});
Vue.use(WechatPlugin);

axios.defaults.baseURL = document.domain == 'localhost' ? 'http://www.yc.com/rest/v1/' : location.protocol + '//' + document.domain+'/rest/v1/';
axios.defaults.baseURL = document.domain == 'localhost' ? 'https://yc.adaxiang.com/rest/v1/' : location.protocol + '//' + document.domain+'/rest/v1/';


Vue.prototype.$axios = axios;
Vue.prototype.axiosPost = function (url, data = {}) {
return new Promise((resolve, reject) => {
axios.post(url, data).then(
response => {
resolve(response.data);
},
err => {
reject(err);
}
)
})
};
Vue.prototype.axiosGet = function (url) {
return new Promise((resolve, reject) => {
axios.get(url).then(response => {
resolve(response.data);
}
).catch(err => {
reject(err)
})
})
};



const routes = [
{
path: '/',
component: Index,
meta: { title: '直播间' },
},
{
path: '/order',
component: Order,
meta: { title: '订单' },
},
{
path: '/order/mylist',
component: MyOrderList,
meta: { title: '我的订单' },
},
{
path: '/order/success',
component: OrderSuccess,
meta: { title: '下单成功' },
},
{
path: '/room',
component: LiveRoom,
meta: { title: '直播间' },
},
{
path: '/addr',
component: Address,
meta: { title: '地址管理' },
},
{
path: '/addr/edit',
component: EditAddress,
meta: { title: '地址管理' },
},
]


const router = new VueRouter({
routes
})


router.beforeEach((to, from, next) => {
if (to.meta.title) {
document.title = to.meta.title
}

next();
});

FastClick.attach(document.body)

Vue.config.productionTip = false

/* eslint-disable no-new */
new Vue({
router,
render: h => h(App)
}).$mount('#app-box')



