export default {
    name: 'order-success',
    data () {
        return {
            orders: {},
            order_id : '',
        }
    },
    created () {
        this.order_id = this.$route.query.order_id;
        this.getOrderList()
    },
    methods: {
        myOrderListPath () {
            this.$router.push({path: '/order/mylist'})
        },
        homePath () {
            this.$router.push({path: '/'})
        },
        getOrderList () {
            if(!that.order_id){
                this.$vux.alert.show({
                    title: '温馨提示',
                    content: "无效订单号"});
                return false;
            }

            let that = this;
            var formdata = new FormData();
            formdata.append('order_id', that.order_id);
            formdata.append('open_id', localStorage.getItem('openid'));

            that.axiosPost('/client/orders', formdata).then((res) => {
                that.orders = res.data[0]
                console.log(that.orders)
            }, (err) => {
                console.log(err)
            })
        }
    }
}



