import importJs from '../../static/js/importJs'
import RoomVideo from "./RoomVideo"
import Product from "./Product"
import Comments from "./Comment"
export default {
    name: 'index',
    components:{
        "room-video": RoomVideo,
        "Product": Product,
        "Comments": Comments,
    },
    data () {
        return {
            showComment: true,
            comment:"",
            total_price: 0,
            lens:{},
            player: {},
            aliplayer_config: {
                id: 'J_prismPlayer',
                width: '100%',
                height: '240px',
                autoplay: false,
                playsinline: true,
                showBuffer: true,
                isLive: true,
                x5_type: true,
                //支持播放地址播放,此播放优先级最高
                source: 'http://aliplay.adaxiang.com/kr/wc001.m3u8',
                cover: '',
                skinLayout: [
                    {name: 'bigPlayButton', align: 'cc', x: 30, y: 80},
                    {name: 'H5Loading', align: 'cc'},
                    //{name: "errorDisplay", align: "tlabs", x: 0, y: 0},
                    //{name: "infoDisplay"},
                    {name: 'tooltip', align: 'blabs', x: 0, y: 56},
                    {name: 'thumbnail'},
                    {
                        name: 'controlBar', align: 'blabs', x: 0, y: 0,
                        children: [
                            {name: 'liveDisplay', align: 'tlabs', x: 15, y: 6},
                            {name: 'progress', align: 'blabs', x: 0, y: 44},
                            {name: 'playButton', align: 'tl', x: 15, y: 12},
                            {name: 'timeDisplay', align: 'tl', x: 10, y: 7},
                            {name: 'fullScreenButton', align: 'tr', x: 10, y: 12},
                            //{name:"subtitle", align:"tr",x:15, y:12},
                            //{name:"setting", align:"tr",x:15, y:12},
                            {name: 'volume', align: 'tr', x: 5, y: 10}

                        ]
                    }
                ]
            },
            roomBasic: {
                room_name: '',
                click_num: '',
                introduce: '',
                content: '',
                cover_img: '',
                coordinate: '',
                addr_url: '',
                addr: '',
                logo_img: '',
            }
        }
    },
    provide() {
        return {
            reload: this.reload
        }
    },
    created() {
        this.getData();
        this.getLens();
        localStorage.setItem('roomid', 9); // 直播间ID
    },
    mounted () {
        $('.prism-big-play-btn').click(function () {
            $('.videoCover').fadeOut()
        })

        var galleryThumbs = new Swiper('.gallery-thumbs', {
            spaceBetween: 10,
            slidesPerView: 4,
            freeMode: true,
            watchSlidesVisibility: true,
            watchSlidesProgress: true,
        })

        var galleryTop = new Swiper('.gallery-top', {
            autoHeight: true, //enable auto height
            spaceBetween: 10,
            navigation: {
                nextEl: '.swiper-button-next',
                prevEl: '.swiper-button-prev',
            },
            thumbs: {
                swiper: galleryThumbs
            }
        })


        var height = $('.prism-player').width()
        $('.prism-player').height(height / 16 * 9)

        $('.liveHome').click(function () {
            $(window).scrollTop(0)
            $(this).fadeOut(800)
        })

        $('.company .remarks p').click(function () {
            if ($(this).hasClass('hide')) {
                $(this).removeClass('hide')
            } else {
                $(this).addClass('hide')
            }
        })

        this.player = new Aliplayer(this.aliplayer_config);
    },
    methods: {
        showMoreInfo (obj) {
            if ($(obj).hasClass('hide')) {
                $(obj).removeClass('hide')
            } else {
                $(obj).addClass('hide')
            }
        },
        switchVideo (pic, url) {
            $('.videoCover').fadeOut()
            this.player.dispose() //销毁
            $('#J_prismPlayer').empty() //id为html里指定的播放器的容器id
            this.aliplayer_config.cover = pic
            this.aliplayer_config.source = url

            this.player = new Aliplayer(this.aliplayer_config)
        },
        switchContent (index) {
            switch (index) {
                case 0:
                    $('.shoppingCarBox').hide()
                    $('.sendMessMenu').hide()
                    break
                case 1:
                    $('.shoppingCarBox').fadeIn()
                    $('.sendMessMenu').hide()
                    break
                case 2:
                    $('.sendMessMenu').fadeIn()
                    $('.shoppingCarBox').hide()
                    break
                case 3:
                    $('.shoppingCarBox').hide()
                    $('.sendMessMenu').hide()
                    break
                default:
            }
        },
        getData(){
            let that = this;
            that.axiosGet("/room/info?id=1").then((res) => {
                that.roomBasic.room_name = res.data.room_name;
                that.roomBasic.logo_img = res.data.logo_img;
                that.roomBasic.click_num = res.data.click_num;
                that.roomBasic.addr_url = res.data.addr_url;
                that.roomBasic.addr = res.data.addr;
                that.roomBasic.coordinate = res.data.coordinate;
                that.roomBasic.cover_img = res.data.cover_img;
                that.roomBasic.introduce = res.data.introduce;
                that.roomBasic.content = res.data.content;
                that.roomBasic.logo_pic = res.data.logo_pic;
            }, (err) => {
                console.log(err);
            });
        },
        getLens(){
            var that = this;
            that.axiosGet("/room/lens?id=9").then((res) => {
                that.$vux.loading.hide();
                if(res.status == 200){
                    that.lens = res.data;
                } else {
                    this.$vux.alert.show({
                        title: '温馨提示',
                        content: res.message});
                }
            }, (err) => {
                that.$vux.loading.hide();
            });
        },
        countPrice(price){
            this.total_price = price;
        },
        tobuy(){
            var that = this;
            this.$refs.product.buy_product(that.roomBasic.id,
                that.roomBasic.room_name,
                that.roomBasic.logo_pic,
                "13750509674",
            );
        },
        subComment(){
            this.$refs.comment.setComment(this.comment);
        },
        reload() {
            this.showComment = false;
            this.$nextTick(function(){
                this.showComment = true;


            })
        }
    }

}





